import SearchHotspot from '@/components/SearchHotspot'

export default {
  components: {
    SearchHotspot
  },
  data () {
    return {
      // 轮播图数据
      swiperdata: [],
      catitems: [],
      floordata: []
    }
  },

  onLoad () {
    this.getSwiperdata()
    this.getCatitems()
    this.getFloordata()
  },
  methods: {
    // 请求轮播图数据
    getSwiperdata () {
      this.$request({
        url: '/api/public/v1/home/swiperdata'
      }).then(data => {
        this.swiperdata = data
      })
    },
    // 分类图片
    getCatitems () {
      this.$request({
        url: '/api/public/v1/home/catitems'
      }).then(data => {
        this.catitems = data
      })
    },
    // 楼层图片
    getFloordata () {
      this.$request({
        url: '/api/public/v1/home/floordata'
      }).then(data => {
        this.floordata = data
      })
    }
  }
}
